.Menu {
  -webkit-box-sizing          : border-box;
          box-sizing          : border-box;
  position            : fixed;
  text-align          : center;
  top                 : 50%;
  -webkit-transform           : translateY(-50%);
      -ms-transform           : translateY(-50%);
          transform           : translateY(-50%);
  width               : 100%;
  margin              : 0;
  padding             : 5rem 0;
  z-index             : 999;
  background          : #000000;
  color               : #eee;
  display             : -ms-flexbox;
  display             : flex;
  -ms-flex-pack     : center;
      justify-content     : center;
  -ms-flex-align         : center;
      align-items         : center;
}

  .Menu.show {
    opacity           : 1;
    -webkit-transform         : translateY(-50%);
        -ms-transform         : translateY(-50%);
            transform         : translateY(-50%);
    -webkit-transition        : opacity 500ms ease-in, -webkit-transform 500ms ease;
    transition        : opacity 500ms ease-in, -webkit-transform 500ms ease;
    -o-transition        : opacity 500ms ease-in, transform 500ms ease;
    transition        : opacity 500ms ease-in, transform 500ms ease;
    transition        : opacity 500ms ease-in, transform 500ms ease, -webkit-transform 500ms ease;
  }

  .Menu.hidden {
    opacity           : 0;
    visibility        : hidden;
    -webkit-transform         : translateY(-100%);
        -ms-transform         : translateY(-100%);
            transform         : translateY(-100%);
    -webkit-transition        : opacity 500ms ease-out,
                        visibility 500ms ease,
                        -webkit-transform 500ms ease;
    transition        : opacity 500ms ease-out,
                        visibility 500ms ease,
                        -webkit-transform 500ms ease;
    -o-transition        : opacity 500ms ease-out,
                        transform 500ms ease,
                        visibility 500ms ease;
    transition        : opacity 500ms ease-out,
                        transform 500ms ease,
                        visibility 500ms ease;
    transition        : opacity 500ms ease-out,
                        transform 500ms ease,
                        visibility 500ms ease,
                        -webkit-transform 500ms ease;
  }

  .Menu .logo {
    margin            : 0;
    padding           : 0;
    font-size         : 3rem;
  }

  .Menu .title {
    margin            : 0;
    padding           : 0;
    font-size         : 2rem;
  }

  .Menu .items {
    margin            : 1rem 0;
  }

  .Menu a.item {
    margin            : 0 1rem;
  }

    .Menu a.item:link, .Menu a.item:visited {
      text-decoration : none;
      color           : #eee;
      -webkit-transition      : color 0.5s ease;
      -o-transition      : color 0.5s ease;
      transition      : color 0.5s ease;
    }

    .Menu a.item:hover {
      color           : #b10;
    }

  .Menu .display {
    margin            : 0;
    padding           : 0;
    background        : #eee;
    color             : red;
  }

  @media (max-width: 400px) {
    .Menu .items {
      font-size       : 1rem;
    }

    .Menu a.item {
      margin          : 0.8rem 0;
      display         : block;
    }
  }


